import React from "react";
import "./LoadingCss.css";

const LoadingScreen = props => {
  if (props.situation === false) {
    return (
      <div className="LoadingScreen">
        <h1>Loading...</h1>
      </div>
    );
  } else {
    return (
      <div className="LoadingScreen">
        <h1>판결 중...</h1>
      </div>
    );
  }
};

export default LoadingScreen;
